<template>
  <ContactForm :showHeader="false">

    <!--  BODY CONTENT  -->
    <div slot="body-content">
      <v-row style="margin-top:-7px" align="center" justify="center">
        <v-col cols="12">
          <v-select
            class="white"
            :rules="rules"
            v-model="setCategory"
            :color="outlineColor"
            :items="getCategories"
            outlined
            @change="categoryDocs"
            :label="category"
        ></v-select>
        </v-col>
        <v-col cols="12">
          <v-text-field
            class="white text-field"
            v-model="setSubject"
            :color="outlineColor"
            outlined
            :label="subject"
            :rules="rules"
            hide-details="auto"
          ></v-text-field>
        </v-col>
        <v-col cols="12">
        <v-textarea
            auto-grow
            class="white"
            v-model="setMessage"
            :color="outlineColor"
            outlined
            :label="message"
            :rules="rules"
            hide-details="auto"
          ></v-textarea>
        </v-col>
      </v-row>

      <v-row class="px-4 pt-1 mt-2" align="center">
        <v-btn @click="toConfirm" :disabled="!sendBtnEnabled" depressed block class="theme-bg">
          {{ nextBtn }}
          <v-icon>mdi-menu-right</v-icon>
        </v-btn>
      </v-row>

      <v-dialog v-model="docsModal" hide-overlay max-width="390">
        <v-card>
          <v-card-title class="body-2">
            {{ findAnswerInDocs }}
          </v-card-title>
          <v-card-text class="body-2 secondary--text">
            <div class="bold-text">
              Category: {{ choosedCategory.label }}
            </div>
          </v-card-text>
          <v-card-actions class="px-4">
            <v-btn @click="toDocs(choosedCategory)" depressed class="px-5 theme-bg">
              {{ toDocsText }}
              <v-icon class="theme-color" small>mdi-chevron-right</v-icon>
            </v-btn>
            <v-btn @click="docsModal = false" outlined class="px-5 secondary--color">Close</v-btn>
          </v-card-actions>
        </v-card>
      </v-dialog>

    </div>
  </ContactForm>
</template>

<script>
import ContactForm from '@/components/ContactForm'
import { mapActions, mapState } from 'vuex'

export default {
  name: 'Contact-page',
  components: {
    ContactForm
  },
  data: () => ({
    docsModal: false,
    rules: [
      value => !!value || 'Required.',
      value => (value && value.length >= 3) || 'Min 3 characters'
    ],
    choosedCategory: {}
  }),
  computed: {
    ...mapState({
      config: ({ appState }) => appState.config,
      formData: ({ userData }) => userData.formData,
      textKeys: ({ appState }) => appState.textKeys
    }),
    sendBtnEnabled () {
      const { formData } = this
      return formData.message.length > 3 && formData.category.length > 1 && formData.subject.length > 3
    },
    setSubject: {
      get: function () {
        return this.formData.subject
      },
      set: function (val) {
        this.setFormData({ subject: val })
      }
    },
    setCategory: {
      get: function () {
        return this.formData.category
      },
      set: function (val) {
        this.setFormData({ category: val })
      }
    },
    setMessage: {
      get: function () {
        return this.formData.message
      },
      set: function (val) {
        this.setFormData({ message: val })
      }
    },
    getCategories () {
      return (this.config.allCategories && this.config.allCategories.map(val => val.label)) || ['{allCategories as array}']
    },
    outlineColor () {
      return getComputedStyle(document.body).getPropertyValue('--theme-bg')
    },
    contactOurSupport () {
      return this.textKeys.contactOurSupport || '{contactOurSupport}'
    },
    startConversationSubText () {
      return this.textKeys.startConversationSubText || '{startConversationSubText}'
    },
    subject () {
      return (this.textKeys.subject || '{subject}') + ' *'
    },
    message () {
      return (this.textKeys.message || '{message}') + ' *'
    },
    category () {
      return (this.textKeys.category || '{category}') + ' *'
    },
    nextBtn () {
      return this.textKeys.nextBtn || '{nextBtn}'
    },
    findAnswerInDocs () {
      return this.textKeys.findAnswerInDocs || '{findAnswerInDocs}'
    },
    toDocsText () {
      return this.textKeys.toDocs || '{toDocs}'
    }
  },
  methods: {
    ...mapActions([
      'setFormData',
      'setSendingMsg',
      'setTicketStatus'
    ]),
    toDocs () {
      window.open(this.choosedCategory.docsUrl)
      this.docsModal = false
    },
    categoryDocs (label) {
      this.docsModal = true
      this.choosedCategory = this.config.allCategories.find(val => val.label === label)
    },
    toConfirm () {
      this.$router.push({ path: '/confirm', query: this.$route.query })
    }
  }
}
</script>

<style lang="scss" scoped>
.message-icon {
  position: absolute;
}
</style>
